import React, { useState } from "react";
import { Board } from "./Board";

export const Game = () => {
  const [gameBoard, setGameBoard] = useState(Array(9).fill(null));
  const [player, setPlayer] = useState(true);
  const marker = player === true ? "X" : "O";
  let status;

  function resetGameBoard() {
    setGameBoard(Array(9).fill(null));
    setPlayer(true);
  }

  function calculateWinner(squares) {
    const lines = [
      [0, 1, 2],
      [3, 4, 5],
      [6, 7, 8],
      [0, 3, 6],
      [1, 4, 7],
      [2, 5, 8],
      [0, 4, 8],
      [2, 4, 6],
    ];
    for (let i = 0; i < lines.length; i++) {
      const [a, b, c] = lines[i];
      if (
        squares[a] &&
        squares[a] === squares[b] &&
        squares[a] === squares[c]
      ) {
        return squares[a];
      }
    }
    if (gameBoard.filter((marker) => marker === null).length === 0){
      return false;
    }
    return null;
  }

  const takeTurn = (i) => {
    const _squares = gameBoard.slice();
    if (calculateWinner(_squares) || _squares[i]) {
      return;
    }
    _squares[i] = marker;
    setGameBoard(_squares);
    setPlayer(!player);
  };

  const winner = calculateWinner(gameBoard);

  if (winner) {
    status = `${winner} is the winner! 🥳`;
  } else if (winner === null) {
    status = `Player ${marker}, make your move.`;
  } else {
    status = `Cats Game! No winner this time!`
  }

  return (
    <div className="game">
      <div className="game-board">
        <Board
          status={status}
          takeTurn={takeTurn}
          gameBoard={gameBoard}
          setGameBoard={setGameBoard}
        />
      </div>
      {winner !== null 
        ? <button onClick={() => {resetGameBoard()}} className="btn btn-primary-outline reset-button mt-2">Reset</button> 
        : null}
      <div className="game-info">
        <div>{/* status */}</div>
        <ol>{/* TODO */}</ol>
      </div>
    </div>
  );
};
